.alert {
    background: #ffdb9b;
    padding: 20px 40px;
    width: 420px;
    position: fixed;
    left: 50%;
    margin-left: -210px;
    top: 50px;
    border-radius: 4px;
    border-left: 8px solid #ffa502;
    pointer-events: none;
    opacity: 0;
    z-index: 2;
}

.added {
    background: #9bffa0;
    padding: 20px 40px;
    width: 420px;
    position: fixed;
    left: 50%;
    margin-left: -210px;
    top: 50px;
    border-radius: 4px;
    border-left: 8px solid #39ff43;
    pointer-events: none;
    opacity: 0;
    z-index: 2;
}

.error {
    background: #ff9b9b;
    padding: 20px 40px;
    width: 420px;
    position: fixed;
    left: 50%;
    margin-left: -210px;
    top: 50px;
    border-radius: 4px;
    border-left: 8px solid #ff4a4a;
    pointer-events: none;
    opacity: 0;
    z-index: 2;
}

.showAlert {
    opacity: 1;
}

.warning_icon {
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-40%);
    font-size: 30px;
}

.message {
    padding: 0 20px;
    font-size: 18px;
    font-weight: 600;
}

.alert.show,
.added.show,
.error.show {
    animation: show_slide 1s ease forwards;
}

.alert.hide,
.added.hide,
.error.hide {
    animation: hide_slide 1s ease forwards;
}

@keyframes show_slide {
    0% {
        transform: translateY(-10%);
    }

    40% {
        transform: translateY(30%);
    }
}

@keyframes hide_slide {
    20% {
        transform: translateY(20%);
    }

    100% {
        transform: translateY(-200%);
    }
}
